import React, { useEffect, useRef, useState } from 'react'

import Table from '../components/table/Table'
import { Paper, Box, Card, Grid, Typography, Button, ButtonGroup, Icon, IconButton, CardContent } from '@material-ui/core'
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import MenuIcon from '@material-ui/icons/Menu';

import API from '../api/Api';
import { format } from 'date-fns'
import { withRouter } from 'react-router-dom';

import '../style/main.scss';

const Index = ({ history }) => {
  const [data, setData] = useState([])

  const columns = [
    {
      field: 'id',
      title: '번호'
    },
    {
      field: 'subject',
      title: '제목'
    },
    {
      field: 'reg_dt',
      title: '등록일',
      render: rowData => format(new Date(rowData.reg_dt), 'yyyy-MM')
    },
  ]

  const init = async () => {
    const data = await API.getNotices()

    console.log(data)

    setData(data)
  }

  const handleRowClick = (id) => {
    return () => {
      history.push(`/user/notice/view/${id}`)
    }
  }

  useEffect(() => {
    init()
  }, [])

  return (
    <div style={{ backgroundColor: '#eee', height: '100vh' }}>
      <Box>
        <Box padding={2} display="flex" justifyContent="space-between" alignItems="center" style={{ background: 'linear-gradient(to right, #2bc0e4, #eaecc6)', height: '4em' }} paddingBottom={2}>
          <Box display="flex" alignItems="center">
            <ChevronLeftIcon></ChevronLeftIcon>
          </Box>
          <Box display="flex" alignItems="center">
            <Typography variant="h5">
              <div>공지사항</div>
            </Typography>
          </Box>
          <Box display="flex" alignItems="center">
            <MenuIcon></MenuIcon>
          </Box>
        </Box>
        <Box padding={2}>
          <Card variant="outlined">
            {(data.length > 0) && data.map((x, i) => (
              <Box
                key={i}
                onClick={handleRowClick(x.id)}
                style={{ cursor: 'pointer' }}
                padding={1}
                display="flex"
                justifyContent="space-between"
                alignItems="center"
              >
                <Box>
                  {x.subject}
                </Box>
                <Box display="flex" alignItems="center">
                  <ChevronRightIcon></ChevronRightIcon>
                </Box>
              </Box>
            )) }
          </Card>
        </Box>
      </Box>
    </div>
  )
}

export default withRouter(Index) 